//----------------------------
// ÏΩúÎ∞± (Callback)
// Ìï®ÏàòÏùò Ïù∏ÏàòÎ°ú ÏÇ¨Ïö©ÎêòÎäî Ìï®Ïàò

// setTimeout(Ìï®Ïàò, ÏãúÍ∞Ñ)


function timeout(cb) {
  setTimeout(() => {
    console.log('EUEUEU')
    cb()
  }, 3000)
}

timeout(() => {
  console.log('Done')
})



